@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  opacity: 0;
  -webkit-animation-delay: 0.125s;
  animation-delay: 0.125s;
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
  -webkit-animation-name: opacityng;
  animation-name: opacityng;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

section.absolute{
  position: fixed;
  pointer-events: none;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  opacity: 0;
  transition: 1s 0.125s;
}
section.absolute.active{
  pointer-events: all;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  background-color: var(--white-75);
  transition: 1s;
}
section.absolute>svg:first-child{
  position: absolute;
  top: 0;
  right: 0;
  margin: 1rem 2rem 0rem 0rem;
}
section.absolute>div{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
section.absolute>div>img{
  opacity: 0;
  -webkit-transform: translateY(1rem) scale(1.25);
          transform: translateY(1rem) scale(1.25);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 72vh;
  box-shadow: 0 0 2rem var(--main-color-50);
  transition: 0.25s;
}
section.absolute.active>div>img{
  opacity: 1;
  -webkit-transform: translateY(0rem) scale(1.25);
          transform: translateY(0rem) scale(1.25);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 72vh;
  box-shadow: 0 0 2rem var(--main-color-50);
  transition: 0.25s 0.375s;
}

section>div{
  width: 100%;
  overflow: hidden;
}

header{
  background-color: #49fcd4;
}

section div img{
  width: calc(100% - 1rem);
  object-fit: cover;
}

section>div:first-child>*>img{
  height: 100vh;
}

div>h1:first-child{
  margin: 0;
}

img.clickable{
  cursor: pointer;
}

.horizontal.flex p.laptop:after{
  background-image: url(../../static/media/laptop-solid.2aea5a7a.svg);
}
.horizontal.flex p.lightning:after{
  background-image: url(../../static/media/bolt-solid.f792c283.svg);
}
.horizontal.flex p.signal:after{
  background-image: url(../../static/media/signal-solid.8311f2d5.svg);
}
.horizontal.flex p.gear:after{
  background-image: url(../../static/media/cog-solid.540ab5dc.svg);
}
.horizontal.flex p.point:after{
  background-image: url(../../static/media/map-marker-alt-solid.d9fe5579.svg);
}
.horizontal.flex p.code:after{
  background-image: url(../../static/media/code-solid.c82229d1.svg);
}

article>div>div{
  width: 100%;
  display: flex;
}
article>div>div>img{
  width: calc(50% - 1rem);
  height: 23vw;
}

article>div>div.fit>*{
  flex: 1 1;
}

article>div>img{
  padding-left: 1rem;
  margin-top: 1rem;
}

article>div>div>*{
  padding-left: 1rem;
  margin-top: 1rem;
}

.turninganimation{
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-name: rotating;
  animation-name: rotating;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.movingupanimation {
  -webkit-animation-delay: 0.125s;
  animation-delay: 0.125s;
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
  -webkit-animation-name: moveup;
  animation-name: moveup;
}
.movingdownanimation {
  -webkit-animation-delay: 0.125s;
  animation-delay: 0.125s;
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
  -webkit-animation-name: movedown;
  animation-name: movedown;
}

@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes opacityng {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes opacityng {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes moveup {
  from {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
  }
  to {
    -webkit-transform: translateY(0rem);
            transform: translateY(0rem);
  }
}

@keyframes moveup {
  from {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
  }
  to {
    -webkit-transform: translateY(0rem);
            transform: translateY(0rem);
  }
}
@-webkit-keyframes movedown {
  from {
    -webkit-transform: translateY(1rem);
            transform: translateY(1rem);
  }
  to {
    -webkit-transform: translateY(0rem);
            transform: translateY(0rem);
  }
}
@keyframes movedown {
  from {
    -webkit-transform: translateY(1rem);
            transform: translateY(1rem);
  }
  to {
    -webkit-transform: translateY(0rem);
            transform: translateY(0rem);
  }
}
@-webkit-keyframes moveright {
  from {
    -webkit-transform: translateX(-1rem);
            transform: translateX(-1rem);
  }
  to {
    -webkit-transform: translateX(0rem);
            transform: translateX(0rem);
  }
}
@keyframes moveright {
  from {
    -webkit-transform: translateX(-1rem);
            transform: translateX(-1rem);
  }
  to {
    -webkit-transform: translateX(0rem);
            transform: translateX(0rem);
  }
}

.clickable{
  cursor: pointer;
}

section>div:nth-last-child(3)>article>div{
  margin-bottom: 0rem;
}
section>div:nth-last-child(2)>header{
  padding-top: 1rem;
}
section>div:nth-last-child(2)>article>div{
  margin-top: 1rem;
}

@media all and (min-width:1910px){
