    display: flex;
    flex-direction: row;
  }
  section>div>*{
    width: 50%;
  }
  section>div:first-child>header{
    padding-top: 12rem;
  }
  section>div:first-child>article>*{
    width: 100%;
  }
  section>div:first-child>header>div>svg{
    width: 52px;
    height: 70px;
  }
  header{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: right;
    padding-top: 6rem;
  }
  header>*{
    margin: 0 5rem 0 6rem;
    padding: 0 5rem 0 6rem;
    float: right;
  }
  header>div{
    position: relative;
  }
  section>div:first-child>header>div::after{
    position: absolute;
    content: '';
    top: 4.75rem;
    right: 0rem;
    height: calc(200% - 4.75rem);
    border-right: 0.125rem solid #43d9b8;
  }
  section>div:not(:first-child)>header>div::after{
    position: absolute;
    content: '';
    top: -6rem;
    right: 0rem;
    height: calc(600% + 6rem);
    border-right: 0.125rem solid #43d9b8;
  }
  section>div:last-child>header>div::after{
    position: absolute;
    content: '';
    top: -6rem;
    right: -0.2rem;
    height: 0;
    border: 0.25rem solid #43d9b8;
    border-radius: 1rem;
  }
  header>div:first-child{
