    right: 3rem;
    border: 0.25rem solid #43d9b8;
    border-radius: 1rem;
  }
  header>div>h1:first-child::before{
    position: absolute;
    content: '';
    top: 4.75rem;
    right: 0rem;
    width: 3rem;
    border-bottom: 0.125rem solid #43d9b8;
  }
  header>div>h2:first-child::after{
    position: absolute;
    content: '';
    top: 1rem;
    right: 3rem;
    border: 0.25rem solid #43d9b8;
    border-radius: 1rem;
  }
  header>div>h2:first-child::before{
    position: absolute;
    content: '';
    top: 1.25rem;
    right: 0rem;
    width: 3rem;
    border-bottom: 0.125rem solid #43d9b8;
  }
  article{
    text-align: left;
  }
  article>div{
    margin: 6.5rem 7rem;
  }
  section>div:last-child>article>div{
    margin: 0rem 7rem 12rem;
  }
  section>div:last-child>article>div>p,
  section>div:last-child>article>div a{
    font-size: medium;
    color: var(--main-color-25);
  }
  .horizontal.flex li{
    width: calc(50% - 5rem);
    padding-left: 5rem;
  }
  .horizontal.flex p{
    position: relative;
    display: flex;
    align-items: center;
  }
  .horizontal.flex p:after{
    position: absolute;
    content: '';
    left: -4.25rem;
    background-position: center center;
    background-repeat: no-repeat;
    height: 1.5rem;
    width: 1.5rem;
  }
}
