    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    overflow: auto;
  }
  .movingdownanimation {
    -webkit-animation-name: moveright;
    animation-name: moveright;
  }
  .movingupanimation{
    -webkit-animation-name: none;
    animation-name: none;
  }
