:root{
    --main-color: #000000;
    --main-color-75: #000000c0;
    --main-color-50: #00000080;
    --main-color-25: #00000040;
    --background-color: #49fcd4;
    --white-75: #ffffffc0;
    --white-50: #ffffff80;
    --button-primary-color: #49fcd4;
    --button-color: #e9e9e9;
    --primary-hover-color: #93ffe7;
    --hover-color: #49fcd4;
    --select-color: #d1fff5;
    --active-color: #44f2cb;
}

*{
    -webkit-tap-highlight-color: transparent;
    font-family: sans-serif;
}

section{
    background-color: white;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
article{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
article>div.flex{
    display: flex;
    flex-direction: column;
}

p, h1, h2, h3, h4, h5, label{
    color: #000000;
    color: var(--main-color);
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
}

h1, h5{
    font-weight: 700;
}
h2{
    line-height: 2.5rem;
    font-weight: 600;
}
h3, h4, b, strong{
    font-weight: 800;
}
h3, h4{
    letter-spacing: 0.125rem;
}
i, em, sup, sub{
    font-weight: 300;
    color: #000000c0;
    color: var(--main-color-75);
}
blockquote{
    margin: 2rem 0 2rem 0;
    padding: 1rem 0rem 1rem 3rem;
    font-style: italic;
    font-weight: 300;
    line-height: 2rem;
    border-left: 4px solid #00000040;
    border-left: 4px solid var(--main-color-25);
}
pre{
    padding: 2rem 2rem 2rem 2rem;
    margin-bottom: 2rem;
    border:  1px solid #00000040;
    border:  1px solid var(--main-color-25);
    border-radius: 0.3rem;
}
p, label{
    line-height: 2rem;
    vertical-align: baseline;
}
p.left, p.left>*{
    float: left;
    line-height: 2.25rem;
}
p.right, p.right>*{
    float: right;
    line-height: 2.25rem;
}
p.left>*, p.right>*{
    width: 11rem;
}
p.left>*{
    margin: 0rem 2rem 0.25rem 0rem;
}
p.right>*{
    margin: 0rem 0rem 0.25rem 2rem;
}
span{
    cursor: pointer;
    color: #000000;
    color: var(--main-color);
    transition: 0.25s;
}
span:hover{
    color: #d1fff5;
    color: var(--select-color);
}
a{
    transition: 0.25s;
    color: #000000;
    color: var(--main-color);
    text-decoration: none;
    border-bottom: 1px dotted #000000;
    border-bottom: 1px dotted var(--main-color);
}
a:hover{
    color: #d1fff5;
    color: var(--select-color);
    border-color: transparent;
}
hr{
    border-top: 1px solid #00000080;
    border-top: 1px solid var(--main-color-50);
}
hr.blank{
    border: 0;
}
ul>li>ul{
    padding: 0 !important;
}
ul.alt, ul.alt_no{
    position: relative;
    padding: 1rem 0rem 1rem 0rem;
    list-style: none;
}
ul.alt:after{
    position: absolute;
    display: block;
    content: " ";
    top: 0px;
    left: 0px;
    width: calc(100% - 1rem);
    height: calc(100% - 2rem);
    margin: 1rem 0rem 1rem 0rem;
    box-sizing: border-box;
    border: 2px solid white;
    pointer-events: none;
}
ul.alt>li{
    margin-right: 1rem;
    box-sizing: border-box;
    border: 1px solid #00000040;
    border: 1px solid var(--main-color-25);
    padding: 0.5rem 0rem;
}
ul.alt_no>li{
    padding: 0.5rem 0rem;
}
ul.horizontal{
    display: flex;    
    padding: 1rem 0rem 0rem 0rem;
}
ul.horizontal>li{
    padding: 0.5rem 0rem 0.5rem 1rem;
    list-style: none;
}
ul.horizontal:not(.flex)>li:first-child{
    padding: 0.5rem 0rem 0.5rem 0rem;
}
ul.horizontal.alt{
    -webkit-transform: translateX(-12.5%) scale(0.75);
            transform: translateX(-12.5%) scale(0.75);
}
ul.horizontal.alt>li{
    border: 0;
    display: flex;
}
ul.horizontal.alt>li:not(:last-child){
    border-right: 1px solid #00000080;
    border-right: 1px solid var(--main-color-50);
}
ul.horizontal.alt>li p, ul.horizontal.alt>li a{
    color: #00000080;
    color: var(--main-color-50);
}
ul.horizontal.alt>li a:hover{
    color: #d1fff5;
    color: var(--select-color);
}
ul.horiend{
    justify-content: flex-end;
}
ul.fit{
    display: flex;
    width: 100%;
    justify-content: space-between;
}
ul.fit>li{
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1 1;
}
ul.fit>li>button{
    flex: 1 1;
}
ul.flex{
    flex-wrap: wrap;
}
.borderbox{
  padding: 1rem;
  border: 1px solid #00000040;
  border: 1px solid var(--main-color-25);
  border-radius: 0.3rem;
}
li>p{
    margin-bottom: 0;
}

div.fit{
    display: flex;
    width: 100%;
}
div.fit>*{
    flex: 1 1;
}

button{
    cursor: pointer;
}
button.fit{
    width: 100%;
}
button, button.default, input[type="reset"], input[type="submit"]{
    display: flex;
    align-items: center;
    border-radius: 0.3rem;
    border: 2px solid #e9e9e9;
    border: 2px solid var(--button-color);
    background-color: white;
    transition: 0.5s;
}
button.primary, input[type="submit"]{
    border: 2px solid #49fcd4;
    border: 2px solid var(--button-primary-color);
    background-color: #49fcd4;
    background-color: var(--button-primary-color);
}
button>a>p, button.primary>a>p{
    transition: 0.5s;
    margin: 0;
    padding: 0;
    font-weight: 700;
    letter-spacing: 0.25rem;
    margin-right: -0.25rem;
}
button.primary>a>p, input[type="submit"]{
    color: black;
}
button>a{
    flex: 1 1;
}
button>a>p{
    text-align: center;
    color: #000000;
    color: var(--main-color);
}
button:not(.disabled):hover, button:not(.disabled):hover>a>p, 
button.default:not(.disabled):hover, button.default:not(.disabled):hover>a>p,
input[type="reset"]:hover{
    color: #44f2cb;
    color: var(--active-color);
}
button.primary:not(.disabled):hover, input[type="submit"]:hover{
    background-color: #d1fff5;
    background-color: var(--select-color);
}
button.primary:not(.disabled):hover>a>p, input[type="submit"]:hover{
    color: black;
}
button.primary:not(.disabled):hover{
    border: 2px solid #93ffe7;
    border: 2px solid var(--primary-hover-color);
    background-color: #93ffe7;
    background-color: var(--primary-hover-color);
}
button:not(.disabled):hover, button.default:not(.disabled):hover
, input[type="submit"]:hover, input[type="reset"]:hover{
    color: #44f2cb;
    color: var(--active-color);
    border: 2px solid #44f2cb;
    border: 2px solid var(--active-color);
}

button:not(.disabled):active, button.default:not(.disabled):active,
input[type="reset"]:active{
    background-color: #d1fff5;
    background-color: var(--select-color);
    border: 2px solid #44f2cb;
    border: 2px solid var(--active-color);
}
button:not(.disabled):active>a>p, button.default:not(.disabled):active>a>p{
    color: #44f2cb;
    color: var(--active-color);
}
button.primary:not(.disabled):active, input[type="submit"]:active{
    border: 2px solid #44f2cb;
    border: 2px solid var(--active-color);
    background-color: #44f2cb;
    background-color: var(--active-color);
}
button.primary:not(.disabled):active>a>p{
    color: black;
}

button svg{
    transition: 0.5s;
    margin-right: 0.5rem;
}
button.primary svg{
    background-color: white;
}
button svg, button a{
    float: left;
    border-bottom: 0px;
}
button:not(.disabled):hover svg, button.default:not(.disabled):hover svg{
    background-color: #d1fff5;
    background-color: var(--select-color);
}
button.primary:not(.disabled):hover svg{
    background-color: white;
}
button.disabled{
    opacity: 0.5;
}
button.disabled a{
    cursor: default;
}

header svg:first-child{
    margin-left: -0.75rem;
