    border: 0;
}
select{
    margin-top: 2rem;
    width: 100%;
}
textarea{
    min-height: 20rem;
}
select, input, textarea{
    border: 0px;
    box-sizing: border-box;
    padding: 1.5rem 1rem;
    color: #000000;
    color: var(--main-color);
    border: 2px solid #e9e9e9;
    border: 2px solid var(--button-color);
    border-radius: 0.3rem;
    font-weight: 400;
}
select:focus-visible, input:focus-visible, textarea:focus-visible{
    outline: none;
    border: 2px solid #49fcd4;
    border: 2px solid var(--button-primary-color);
}
label{
    cursor: pointer;
}
input[type="radio"], input[type="checkbox"]{
    margin-right: 1rem;
}
input[type="radio"]::before{
    width: 1.5rem;
    height: 1.5rem;
    display: block;
    content: url(../../static/media/check-solid.2f94d1e5.svg);
    position: relative;
    top: -0.375rem;
    left: -0.375rem;
    color: white;
    background-color: white;
    border-radius: 50%;
    border:  1px solid #000000;
    border:  1px solid var(--main-color);
}
input[type="radio"]:checked::before{
    background-color: #000000;
    background-color: var(--main-color);
    border:  1px solid #000000;
    border:  1px solid var(--main-color);
}
input[type="checkbox"]::before{
    width: 1.5rem;
    height: 1.5rem;
    display: block;
    content: url(../../static/media/check-solid.2f94d1e5.svg);
    position: relative;
    top: -0.375rem;
    left: -0.375rem;
    color: white;
    background-color: white;
    border-radius: 5px;
    border:  1px solid #000000;
    border:  1px solid var(--main-color);
}
input[type="checkbox"]:checked::before{
    background-color: #000000;
    background-color: var(--main-color);
    border:  1px solid #000000;
    border:  1px solid var(--main-color);
}
textarea{
    width: 100%;
    margin-top: 1rem;
    resize: none;
}
img{
    width: 99%;
    border-radius: 0rem;
}
form .wrap{
    display: flex;
}
form .wrap>*{
    flex: 1 1;
}

